import React from 'react'
import { 
  Container, 
  Row, 
  Col,   
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink} from 'reactstrap'

import logo from '../img/logo.png'
import yf from '../img/yf.png'
import tf from '../img/tf.png'
import ff from '../img/ff.png'
import inf from '../img/inf.png'


const styles = {
  logo:{
    width: 205,
    height: 90
  },
  soc:{
    width: 40,
    height: 40
  }
}

function Footer (props) {

    return (
      <>
      <div className="footer">
        <Container>
          <Row>
            <Col>
              <img src={logo} style={styles.logo} className="footer_logo"/>
            </Col>
          </Row>
          <Row>
            <Col>
                  <Nav className="mx-auto">
                    <NavItem>
                      <NavLink href="https://www.youtube.com/user/RiotGamesInc" target="_blank"><img src={yf} style={styles.soc} /></NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink href="https://twitter.com/kda_music" target="_blank"><img src={tf} style={styles.soc} /></NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink href="https://www.facebook.com/leagueoflegends" target="_blank"><img src={ff} style={styles.soc} /></NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink href="https://www.instagram.com/kda_music/" target="_blank"><img src={inf} style={styles.soc} /></NavLink>
                    </NavItem>
                  </Nav>
                  <Nav className="mx-auto"></Nav>
                  <Nav className="mx-auto">
                    <NavItem>
                      <NavLink href="#" target="_blank">Privacy Notice</NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink href="#" target="_blank">Terms of Service</NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink href="#" target="_blank">Cookie Preferences</NavLink>
                    </NavItem>
                  </Nav>
            </Col>
          </Row>
        </Container>
        

      </div>
      </>
    )
}

export default Footer